<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <v-card class="mb-4">
          <v-card-title class="text-h5">
            
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="saveReport" :loading="saving">
              
            </v-btn>

          </v-card-title>


          <v-card-text>
            <v-row>
              <!--  -->
              <v-col cols="12" md="4">
                <v-card variant="outlined">
                  <v-card-title class="text-h6"></v-card-title>
                  <v-card-text>
                    <div class="mb-4">
                      <strong>:</strong> {{ experimentData.title }}
                    </div>
                    <div class="mb-4">
                      <strong>:</strong> {{ experimentData.date }}
                    </div>
                    <div class="mb-4">
                      <strong>:</strong>
                      <p>{{ experimentData.objective }}</p>
                    </div>
                    <div class="mb-4">
                      <strong>:</strong>
                      <v-list density="compact">
                        <v-list-item v-for="step in experimentData.steps" :key="step.stepId">
                          <template v-slot:title>
                            <v-icon v-if="step.checked" color="primary" class="mr-2">mdi-check</v-icon>
                            <v-icon v-else color="red" class="mr-2">mdi-flask-empty-off-outline</v-icon>
                            Step {{ step.stepId }}
                          </template>
                          <template v-slot:subtitle>
                            {{ step.description }}
                          </template>
                        </v-list-item>
                      </v-list>
                    </div>
                  </v-card-text>
                </v-card>
              </v-col>

              <!--  -->
              <v-col cols="12" md="8">
                <v-form ref="form">
                  <v-text-field v-model="report.title" label="" required class="mb-4"></v-text-field>

                  <v-tabs v-model="activeTab" class="mb-4">
                    <v-tab value="abstract"></v-tab>
                    <v-tab value="method"></v-tab>
                    <v-tab value="result"></v-tab>
                    <v-tab value="discussion"></v-tab>
                  </v-tabs>

                  <v-window v-model="activeTab">
                    <v-window-item value="abstract">
                      <v-textarea v-model="report.abstract" label="" rows="6" hint=""
                        persistent-hint></v-textarea>
                    </v-window-item>

                    <v-window-item value="method">
                      <v-textarea v-model="report.method" label="" rows="10" hint=""
                        persistent-hint></v-textarea>
                    </v-window-item>

                    <v-window-item value="result">
                      <v-textarea v-model="report.result" label="" rows="10" hint=""
                        persistent-hint></v-textarea>

                      <v-file-input v-model="resultFiles" label="" multiple accept="image/*"
                        prepend-icon="mdi-camera" class="mt-4" @change="handleFileUpload"></v-file-input>

                      <div v-if="report.figures.length > 0" class="mt-4">
                        <div v-for="(figure, index) in report.figures" :key="index" class="mb-2">
                          <v-img :src="figure.url" max-height="200"></v-img>
                          <v-text-field v-model="figure.caption" :label="`${index + 1}`" dense></v-text-field>
                        </div>
                      </div>
                    </v-window-item>

                    <v-window-item value="discussion">
                      <v-textarea v-model="report.discussion" label="" rows="10" hint=""
                        persistent-hint></v-textarea>
                    </v-window-item>
                  </v-window>
                </v-form>
                <!-- Gemini  -->
                <v-btn :loading="!valid" color="primary" @click="processWithGemini">
                  AI
                </v-btn>

                <ExperimentEditor v-if="showEditor" :experiment-data="generatedData" @saved="handleSaved" />

                <ExperimentEditor v-model="showEditor" :experiment-data="generatedData" @saved="handleSaved" />

              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>

  <v-btn class="floating-btn" color="primary" icon="mdi-home" size="x-large" @click="returnDashboard"></v-btn>

</template>

<script>
import { ref, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { doc, getDoc, setDoc, getFirestore } from 'firebase/firestore'
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage'

const { GoogleGenerativeAI } = require("@google/generative-ai");

export default {
  name: 'ExperimentReport',
  props: {
    experimentData: {
      type: Object,
      required: true
    }
  },

  setup(props) {
    const valid = ref(true)
    const showEditor = ref(false)
    const route = useRoute()
    const router = useRouter()
    const form = ref(null)
    const activeTab = ref('abstract')
    const saving = ref(false)
    const resultFiles = ref([])
    let generatedData = null

    const toggleEditor = () => {
      showEditor.value = !showEditor.value;  // `this`
    }

    const report = ref({
      title: '',
      abstract: '',
      method: '',
      result: '',
      discussion: '',
      figures: [],
      experimentId: route.params.id,
      createdAt: null,
      updatedAt: null
    })

    // 
    onMounted(async () => {
      const db = getFirestore()
      const reportRef = doc(db, 'reports', route.params.id)
      const reportDoc = await getDoc(reportRef)

      if (reportDoc.exists()) {
        const data = reportDoc.data()
        report.value = { ...report.value, ...data }
      }
    })

    const handleFileUpload = async (files) => {
      if (!files) return

      const storage = getStorage()

      for (const file of files) {
        const fileRef = storageRef(storage, `reports/${route.params.id}/${file.name}`)
        await uploadBytes(fileRef, file)
        const url = await getDownloadURL(fileRef)

        report.value.figures.push({
          url,
          caption: '',
          filename: file.name
        })
      }
    }

    const saveReport = async () => {
      try {
        saving.value = true
        const db = getFirestore()
        const reportRef = doc(db, 'reports', route.params.id)

        const now = new Date()
        report.value.updatedAt = now
        if (!report.value.createdAt) {
          report.value.createdAt = now
        }

        await setDoc(reportRef, report.value)
        saving.value = false

        // 
        alert('')
      } catch (error) {
        // console.error('Error saving report:', error)
        alert('')
        saving.value = false
      }
    }

    const returnDashboard = () => {
      router.push('/dashboard')
    }

    const processWithGemini = async () => {
      valid.value = false
      const combinedText = props.experimentData
      if (!combinedText) {
        // console.log('No text to process')
        return
      }

      // Gemini API
      const genAI = new GoogleGenerativeAI(process.env.VUE_APP_GEMINI_API_KEY)
      const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });
      const prompt = `output JSON only (without writing \`\`\`json)
  JSON SchemaJSON {  \` 

  ${combinedText.date}, ${combinedText.objective}, ${combinedText.precautions}, ${combinedText.steps}, ${combinedText.title}
  : ${report.value.title}, ${report.value.abstract}, ${report.value.method}, ${report.value.result}, ${report.value.discussion}, ${JSON.stringify(report.value.figures)}

  ()
  {
    "title": "${report.value.title || ''}",
    "abstract": "${report.value.abstract || ''}",
    "method": "${report.value.method || ''}",
    "result": "${report.value.result || ''}",
    "discussion": "${report.value.discussion || ''}",
    "figures": ${JSON.stringify(report.value.figures) || '[]'},
    "experimentId": "${report.value.experimentId}",
    "createdAt": "${report.value.createdAt || null}",
    "updatedAt": "${report.value.updatedAt || null}"
  }`

      alert()
      try {
        const result = await model.generateContent(prompt)
        const response = result.response
        const jsonText = response.text()
        try {
          const parsedData = JSON.parse(jsonText)
          // 
          report.value = parsedData
        } catch (parseError) {
          alert('')
        }
      } catch (error) {
        alert('')
      }
      valid.value = true
    }

    const handleSaved = () => {
      // 
      showEditor.value = false
      generatedData.value = null
      router.push('/dashboard')
      // 
    }

    return {
      showEditor,
      generatedData,
      toggleEditor,
      props,
      form,
      report,
      activeTab,
      saving,
      resultFiles,
      handleFileUpload,
      saveReport,
      returnDashboard,
      processWithGemini,
      handleSaved,
      valid
    }
  }
}
</script>

<style scoped>
.v-card-text {
  height: calc(100vh - 200px);
  overflow-y: auto;
}

.floating-btn {
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 100;
}
</style>
