<!-- MessageDialog.vue -->
<template>
  <v-dialog v-model="isActive" max-width="400">
    <v-card>
      <v-card-text>
        {{ message }}
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn variant="text" @click="closeDialog">
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { ref, watch } from 'vue'

export default {
  name: 'MessageDialog',
  props: {
    message: {
      type: String,
      default: ''
    },
    show: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const isActive = ref(props.show)

    // `props.message` 
    watch(
      () => props.message,
      (newValue) => {
        if (newValue) {
          isActive.value = true
        }
      }
    )

    // 
    const closeDialog = () => {
      isActive.value = false
      emit('update:show', false) // 
    }

    // `show` `isActive` 
    watch(
      () => props.show,
      (value) => {
        isActive.value = value
      }
    )

    return {
      isActive,
      closeDialog,
      // message: props.message // props.message 
    }
  }
}
</script>
