<template>
  <v-container>
    <!--  -->
    <v-row v-if="loading" justify="center" align="center" style="height: 80vh;">
      <v-col cols="12" class="text-center">
        <v-progress-circular indeterminate color="primary" size="64"></v-progress-circular>
        <div class="mt-4">...</div>
      </v-col>
    </v-row>

    <!--  -->
    <v-row v-else-if="error" justify="center" align="center" style="height: 80vh;">
      <v-col cols="12" class="text-center">
        <v-alert type="error" title="">
          {{ error }}
          <template v-slot:append>
            <v-btn color="error" @click="loadExperiment"></v-btn>
          </template>
        </v-alert>
      </v-col>
    </v-row>

    <!--  -->
    <v-row v-else-if="!experimentData" justify="center" align="center" style="height: 80vh;">
      <v-col cols="12" class="text-center">
        <v-alert type="warning" title="">
          
          <template v-slot:append>
            <v-btn color="primary" @click="goBack"></v-btn>
          </template>
        </v-alert>
      </v-col>
    </v-row>

    <!-- ExperimentReport  -->
    <template v-else>
      <v-row class="mb-4">
        <v-col cols="12">
          <v-btn icon="mdi-arrow-left" variant="text" @click="goBack"></v-btn>
          <span class="text-h5 ml-2"></span>
        </v-col>
      </v-row>

      <ExperimentReport :experiment-data="experimentData" @saved="handleReportSaved" />
    </template>

    <!--  -->
    <v-snackbar v-model="showSuccessSnackbar" color="success" timeout="3000">
      
    </v-snackbar>
  </v-container>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { doc, getDoc, getFirestore } from 'firebase/firestore'
import ExperimentReport from './ExperimentReport.vue'

export default {
  name: 'WriteReportScreen',

  components: {
    ExperimentReport
  },

  setup() {
    const route = useRoute()
    const router = useRouter()
    const experimentData = ref(null)
    const loading = ref(true)
    const error = ref(null)
    const showSuccessSnackbar = ref(false)

    const loadExperiment = async () => {
      loading.value = true
      error.value = null

      try {
        const experimentId = route.params.id
        if (!experimentId) {
          throw new Error('ID')
        }

        const db = getFirestore()
        const experimentRef = doc(db, 'experiments', experimentId)
        const experimentDoc = await getDoc(experimentRef)

        if (!experimentDoc.exists()) {
          throw new Error('')
        }

        experimentData.value = {
          id: experimentDoc.id,
          ...experimentDoc.data()
        }
      } catch (e) {
        // console.error(':', e)
        error.value = e.message || ''

        // 
        router.push(`/experiment/${route.params.id}`)
      } finally {
        loading.value = false
      }
    }

    const goBack = () => {
      router.push(`/experiment/${route.params.id}`)
    }

    const handleReportSaved = () => {
      showSuccessSnackbar.value = true
      // 
      setTimeout(() => {
        router.push('/dashboard')
      }, 3000)
    }

    onMounted(() => {
      loadExperiment()
    })

    return {
      experimentData,
      loading,
      error,
      showSuccessSnackbar,
      loadExperiment,
      goBack,
      handleReportSaved
    }
  }
}
</script>
